<template>
  <div class="con">
    <div class="qv-head">
      <img src="@/assets/guan-close.png" @click="goBack" />
      <div class="qv-head-text">地区</div>
    </div>
    <div class="select-all">
      <div class="select-all-top">
        <!-- 头部 -->
        <div class="select-all-titles">
          <div class="select-all-titles-l" @click="goChoose">
            <div class="title-text">
              {{ selectProvince }}
              <div class="line-abso"></div>
            </div>
            <img src="@/assets/ko.png" />
          </div>
          <div class="select-all-titles-m">
            <div class="title-text">
              {{ selectCity }}
              <div class="line-abso"></div>
            </div>
            <img src="@/assets/ko.png" />
          </div>
        </div>
        <!-- 列表 -->
        <div class="lie-all">
          <div v-if="provinceId == '' && provinceList.length != 0">
            <div
              class="lie-all-item"
              v-for="(item, index) in provinceList"
              :key="index"
              @click="goTab(index, item.id, item.name)"
            >
              <div
                class="lie-all-item-l"
                :class="activeIndex == index ? 'actived' : ''"
              >
                {{ item.name }}
              </div>
              <img src="@/assets/icon_done.png" v-if="activeIndex == index" />
            </div>
          </div>
          <div v-if="provinceId != '' && cityList.length != 0">
            <div
              class="lie-all-item"
              v-for="(item, index) in cityList"
              :key="index"
              @click="goTab1(index, item.id, item.name)"
            >
              <div
                class="lie-all-item-l"
                :class="activeIndex1 == index ? 'actived' : ''"
              >
                {{ item.name }}
              </div>
              <img src="@/assets/icon_done.png" v-if="activeIndex1 == index" />
            </div>
          </div>
          <div
            class="no-content"
            v-if="
              (provinceId != '' && cityList.length == 0) ||
              (provinceId == '' && provinceList.length == 0)
            "
          >
            <img src="@/assets/noData.png" />
            <div class="no-content-text">暂无数据</div>
          </div>
        </div>
      </div>
    </div>
    <div class="select-footer">
      <div class="select-footer-l" @click="goAgain">重置</div>
      <div class="select-footer-r" @click="goConfirm">确定</div>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  name: "zhaoMoreCity",
  data() {
    return {
      activeIndex: 9999999,
      activeIndex1: 9999999,
      provinceList: [],
      provinceId: "",
      cityList: [],
      selectProvince: "选择省份",
      selectCity: "选择城市",
      cityId: "",
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    goAgain() {
      this.cityId = "";
      this.selectProvince = "选择省份";
      this.selectCity = "选择城市";
      this.provinceId = "";
      this.cityList = [];
      this.activeIndex = 9999999;
      this.activeIndex1 = 9999999;
      this.provinceList = [];
      this.getSS();
    },
    goConfirm() {
      this.$router.go(-1);
    },
    goChoose() {
      this.activeIndex = 9999999;
      this.activeIndex1 = 9999999;
      this.provinceId = "";
      this.selectProvince = "选择省份";
      this.selectCity = "选择城市";
      this.provinceList = [];
      this.cityList = [];
      this.cityId = "";
      this.getSS();
    },
    goTab(index, id, name) {
      console.log(index, id, name);
      this.activeIndex = index;
      this.provinceId = id;
      this.selectProvince = name;
      this.cityList = [];
      this.activeIndex = 9999999;
      this.activeIndex1 = 9999999;
      this.cityId = "";
      this.getCities();
    },

    goTab1(index, id, name) {
      this.activeIndex1 = index;
      this.selectCity = name;
      this.cityId = id;
    },
    getSS() {
      this.$axios
        .post("/f/treeData/ajax_getProvinceList")
        .then((res) => {
          console.log(res, "省市");
          if (res.data.state == 1) {
            this.provinceList = res.data.data;
          } else {
            this.provinceList = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getCities() {
      const params = {
        parentid: this.provinceId,
      };
      console.log(params);
      this.$axios
        // .get("/f/treeData/ajax_getCityList?parentid=" + this.provinceId)
        .post("/f/treeData/ajax_getCityList", qs.stringify(params))
        .then((res) => {
          console.log(res, "城市");
          if (res.data.state == 1) {
            this.cityList = res.data.data;
          } else {
            this.cityList = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    this.getSS();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
.qv-head {
  width: 7.5rem;
  height: 0.88rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
}
.qv-head img {
  position: absolute;
  left: 0.52rem;
  width: 0.32rem;
  height: 0.32rem;
  top: 0.28rem;
}
.qv-head-text {
  font-family: PingFangSC-Semibold;
  font-size: 0.32rem !important;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #2b2e34;
}
.select-all {
  width: 7.5rem;
  background-color: rgba(255, 255, 255, 0.7);
  border-top: 0.01rem solid #eeeeee;
  padding: 0.4rem 0.52rem 3.12rem 0.52rem;
  box-sizing: border-box;
  border-top: 0.01rem solid #eeeeee;
}

.select-all-titles {
  width: 6.46rem;
  display: flex;
  margin-bottom: 0.56rem;
}

.select-all-titles-l {
  display: flex;
  align-items: center;
}

.select-all-titles-l img {
  width: 0.16rem;
  height: 0.26rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.select-all-titles-m {
  display: flex;
  align-items: center;
}

.select-all-titles-m img {
  width: 0.16rem;
  height: 0.26rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.select-all-titles-r {
  font-family: PingFangSC-Medium;
  font-size: 0.34rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: -0.02rem;
  color: #2b2e34;
}

.title-text {
  font-family: PingFangSC-Medium;
  font-size: 0.34rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: -0.02rem;
  color: #2b2e34;
  position: relative;
}

.line-abso {
  position: absolute;
  width: 100%;
  height: 0.08rem;
  background-image: linear-gradient(
    90deg,
    rgba(255, 224, 89, 0.6) 0%,
    #febd2d 100%
  );
  border-radius: 0.05rem;
  bottom: 0rem;
  left: 0;
  content: "";
  opacity: 0.4;
}

.lie-all {
  width: 6.46rem;
}

.lie-all-item {
  width: 6.46rem;
  height: 0.9rem;
  border-bottom: 0.01rem solid #eeeeee;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.lie-all-item-l {
  font-family: PingFangSC-Medium;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #7a7c8b;
}

.actived {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #188463 !important;
}

.lie-all-item img {
  width: 0.28rem;
  height: 0.2rem;
}

.select-footer {
  width: 7.5rem;
  height: 2rem;
  background-color: rgba(255, 255, 255);
  padding: 0.36rem 0.32rem 0.62rem 0.32rem;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.select-footer-l {
  width: 2.72rem;
  height: 0.8rem;
  border: 0.02rem #4760d7 solid;
  text-align: center;
  line-height: 0.8rem;
  font-family: PingFangSC-Regular;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #4760d7;
  border-radius: 0.08rem;
}

.select-footer-r {
  width: 3.96rem;
  height: 0.8rem;
  background-color: #4760d7;
  border-radius: 0.08rem;
  text-align: center;
  line-height: 0.8rem;
  font-family: PingFangSC-Regular;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #ffffff;
}

.no-content {
  width: 6.46rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2.3rem;
  box-sizing: border-box;
}

.no-content img {
  width: 3.34rem;
  height: 1.76rem;
  margin-bottom: 0.7rem;
}

.no-content-text {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #9fa4b3;
}
</style>


